<template>
    <div class="content">
        <div class="control-bar">
            <div class="settings-item">
                <div class="setting-name">Graph Data Set</div>
                <div class="setting-value">
                    <el-select v-model="Graph_Data_Set_value"
                               placeholder="Please select Graph Data"
                               @change="initOriginGraph">
                        <el-option
                                v-for="item in GraphDataSet_list"
                                :key="item"
                                :label="item"
                                :value="item">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="settings-item">
                <div class="setting-name">Algorithm</div>
                <div class="setting-value">
                    <el-select v-model="algorithm_value"
                               placeholder="Please select algorithm"
                               @change="initAgorithmParams">
                        <el-option
                                v-for="item in algorithm_list"
                                :key="item.label"
                                :label="item.label"
                                :value="item.label">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="settings-item">
                <div class="setting-name">Parameters</div>
                <div class="setting-value param-option">
                    <span>sampling rate</span>
                    <input class="option_input" type="text"
                           v-model="param_settings.rate">
                    <div class="other_options">
                        <div class="option_item"
                             v-for="(option,index) in get_option_labels"
                             :key="index">
                            <el-divider direction="vertical"></el-divider>
                            <span>{{option}}</span><input class="option_input"
                                                          type="text"
                                                          v-model="param_settings[option]">
                        </div>
                    </div>
                </div>
            </div>
            <input type="button" id="run-btn" value="Run" @click="run_sampling">
        </div>
        <div class="graph-container">
            <div class="tooltip" v-show="node_Id">{{node_Id}}<br/>{{rank_Id!==" "?rank_Id:""}}
            </div>
            <div class="graph-box origin-graph">
                <span id="origin-tag">Original Graph</span>
                <span id="origin-info-tag" v-show="current_graph===''">Please select Graph Data Set</span>

                <div role="tooltip" id="no_tip" aria-hidden="false"
                     class="el-popover el-popper el-popover--plain my-no-tip"
                     tabindex="0"
                     x-placement="bottom">
                    There are no <b>{{select_minotype}}s</b> in this graph
                </div>

                <div class="loading-div-origin" v-show="LoadingOrigin"
                     v-loading="LoadingOrigin">
                </div>
            </div>
            <div class="graph-box sampling-graph" id="sampling-graph">
                <span id="sampling-tag">Graph Samples</span>
                <div class="loading-div" v-show="LoadingSampling"
                     v-loading="LoadingSampling"
                     style="left: 1%;">
                </div>
                <i class="el-icon el-icon-arrow-left"
                   v-show="queue_num>=3&&samping_hover&&left_num!==1"
                   @click="switch_view('left')"
                   style="color:#999999;font-size:25px;z-index: 2000;cursor: pointer;position: absolute;top: 45%;left: 0.8%"></i>
                <i class="el-icon el-icon-arrow-right"
                   v-show="queue_num>=3&&samping_hover&&right_num!==queue_num"
                   @click="switch_view('right')"
                   style="color:#999999;font-size:25px;z-index: 2000;cursor: pointer;position: absolute;top: 45%;right: 0.8%;"></i>
                <div v-show="queue_num>=2"
                     style="position:absolute;left:50%;top:4%;width:1px;height:93%;border-right:1px dashed #CCCCCC;">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "../utils/axios"
    import * as d3 from "d3"

    //鱼眼组件对象
    let d3_fisheye = {
        scale: function (scaleType) {
            return d3_fisheye_scale(scaleType(), 3, 0);
        },
        circular: function () {
            var radius = 200,
                distortion = 2,
                k0,
                k1,
                focus = [0, 0];

            function fisheye(d) {
                var dx = d.cx - focus[0],
                    dy = d.cy - focus[1],
                    dd = Math.sqrt(dx * dx + dy * dy);
                if (!dd || dd >= radius) return {x: d.cx, y: d.cy, z: 1};
                var k = k0 * (1 - Math.exp(-dd * k1)) / dd * .75 + .25;
                return {
                    x: focus[0] + dx * k,
                    y: focus[1] + dy * k,
                    z: Math.min(1, 10)
                };
            }

            function rescale() {
                k0 = Math.exp(distortion);
                k0 = k0 / (k0 - 1) * radius;
                k1 = distortion / radius;
                return fisheye;
            }

            fisheye.radius = function (_) {
                if (!arguments.length) return radius;
                radius = +_;
                return rescale();
            };
            fisheye.distortion = function (_) {
                if (!arguments.length) return distortion;
                distortion = +_;
                return rescale();
            };
            fisheye.focus = function (_) {
                if (!arguments.length) return focus;
                focus = _;
                return fisheye;
            };
            return rescale();
        }
    };

    //鱼眼缩放辅助函数1
    function d3_fisheye_scale(scale, d, a) {
        function fisheye(_) {
            var x = scale(_),
                left = x < a,
                v,
                range = d3.extent(scale.range()),
                min = range[0],
                max = range[1],
                m = left ? a - min : max - a;
            if (m == 0) m = max - min;
            return (left ? -1 : 1) * m * (d + 1) / (d + (m / Math.abs(x - a))) + a;
        }

        fisheye.distortion = function (_) {
            if (!arguments.length) return d;
            d = +_;
            return fisheye;
        };

        fisheye.focus = function (_) {
            if (!arguments.length) return a;
            a = +_;
            return fisheye;
        };

        fisheye.copy = function () {
            return d3_fisheye_scale(scale.copy(), d, a);
        };

        fisheye.nice = scale.nice;
        fisheye.ticks = scale.ticks;
        fisheye.tickFormat = scale.tickFormat;

        return rebind(fisheye, scale, "domain", "range");
    }

    //鱼眼辅助函数2
    function rebind(e, t) {
        var n = 1, r = arguments.length, i;
        while (++n < r)
            e[i = arguments[n]] = a(e, t, t[i]);
        return e
    }

    //字符串格式化辅助函数
    // 事实证明这名没有什么用，不如用ES6的反引号语法`{}`
    String.prototype.format = function (args) {
        var result = this;
        if (arguments.length < 1) {
            return result;
        }
        var data = arguments;
        if (arguments.length === 1 && typeof (args) == "object") {
            data = args;
        }
        for (var key in data) {
            var value = data[key];
            if (undefined !== value) {
                result = result.replace("{" + key + "}", value);
            }
        }
        return result;

    };

    export default {
        name: 'Home',
        data() {
            return {
                GraphDataSet_list: ['Cagrqc', 'Cpan', 'Eurosis'],
                Graph_Data_Set_value: '',
                algorithm_list: {
                    BF: {label: 'BF', options: []},
                    FF: {label: 'FF', options: []},
                    MCGS: {label: 'MCGS', options: ['alpha', 'beta', 'loss weight']},
                    RAS: {label: 'RAS', options: []},
                    RDN: {label: 'RDN', options: []},
                    RMSC: {label: 'RMSC', options: []},
                    TIES: {label: 'TIES', options: []}
                },
                algorithm_value: '',
                rate_list: [0.1, 0.2, 0.3, 0.4],
                param_settings: {
                    'rate': '',
                    'alpha': '1',
                    'beta': '2',
                    'loss weight': '1,0,0'
                },
                left_num: 1,
                right_num: 2,
                queue_num: 0,
                samping_hover: true,
                current_graph: '',
                LoadingSampling: false,
                LoadingOrigin: false,
                label_tip: null,
                node_Id: false,
                rank_Id: false,
                anomaly_total: {},
                select_minotype: '',
                graph_record: {}
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.initSetting();
            })
        },
        methods: {
            initSetting() {
                var self = this;
                // 设置原始图和采样图的框的高度大小
                let screenWidth = document.documentElement.clientWidth || document.body.clientWidth;
                let screenHeight = document.documentElement.clientHeight || document.body.clientHeight;
                document.querySelector(".origin-graph").style.height = screenHeight * 0.80 + "px";
                var sampling_graph = document.querySelector(".sampling-graph");
                sampling_graph.style.height = screenHeight * 0.80 + "px";
                sampling_graph.onmouseenter = function () {
                    self.samping_hover = true;
                };
                sampling_graph.onmouseleave = function () {
                    self.samping_hover = false;
                };

                this.label_tip = document.querySelector('.tooltip');
            },
            //重置算法参数设置
            initAgorithmParams() {
                this.param_settings = {
                    'rate': '0.3',
                    'alpha': '1',
                    'beta': '2',
                    'loss weight': '1,0,0',
                }
            },
            //添加鱼眼效果的渲染原始图svg
            //使用Json格式加载
            loading_Original_Graph() {
                var self = this;

                d3.json(self.origin_fisheye_file).then(function (data) {
                    // 删除之前的
                    d3.select(".origin-graph").selectAll("svg").remove();

                    //要先从data里获取svg头信息，用于设置svg元素属性
                    var origin_svg = d3.select(".origin-graph").append("svg")
                        .attr("width", data.svg_width)
                        .attr("height", data.svg_height)
                        .attr("viewBox", data.svg_viewBox);

                    var g = origin_svg.append('g');

                    var link = g.selectAll(".link")
                        .data(data.links)
                        .enter().append("line")
                        .attr("class", "link")
                        .attr("x1", function (d) {
                            return data.nodes[d.source].cx
                        })
                        .attr("y1", function (d) {
                            return data.nodes[d.source].cy
                        })
                        .attr("x2", function (d) {
                            return data.nodes[d.target].cx
                        })
                        .attr("y2", function (d) {
                            return data.nodes[d.target].cy
                        }).style("stroke", "#bcbcbc");

                    var node = g.selectAll(".node")
                        .data(data.nodes)
                        .enter().append("circle")
                        .attr("class", function (d) {
                            return d.id;
                        })
                        .attr("cx", function (d) {
                            return d.cx;
                        })
                        .attr("cy", function (d) {
                            return d.cy;
                        })
                        .attr("r", function (d) {
                            return d.r * 1.6
                        })
                        .style("fill", "#cccccc")
                        .style("stroke", "#666666");

                    let parentNode = document.querySelector(".origin-graph");
                    parentNode.setAttribute("open-fisheye", true);
                    var parent_Width = parentNode.clientWidth;
                    var parent_Height = parentNode.clientHeight;
                    let svgWidth = origin_svg.attr("width").replace("px", "");
                    let svgHeight = origin_svg.attr("height").replace("px", "");
                    var margin = {left: 20, right: 20, top: 20, bottom: 20};
                    var scaleNumber = d3.min([(parent_Width - margin.left - margin.right) / svgWidth, (parent_Height - margin.top - margin.bottom) / svgHeight]);
                    var origin_svgWidth = svgWidth * scaleNumber;
                    var origin_svgHeight = svgHeight * scaleNumber;
                    origin_svg
                        .attr("width", origin_svgWidth)
                        .attr("height", origin_svgHeight)
                        .style("position", "absolute")
                        .style("left", (parent_Width - origin_svgWidth) / 2 + "px")
                        .style("top", (parent_Height - origin_svgHeight) / 2 + "px");

                    //添加鼠标hover标注事件
                    g.selectAll("circle")
                        .on("mouseover", function () {
                            if (self.select_minotype) {
                                var current_node = d3.select(this);
                                var node_id = parseInt(current_node.attr("class").replace("id_", ""));
                                var index = (self.graph_record['minority'][self.select_minotype]).findIndex(item => item === node_id) + 1;
                                self.label_tip.style.left = d3.event.pageX + 15 + "px";
                                self.label_tip.style.top = d3.event.pageY + 5 + "px";
                                if (index > 0) {
                                    self.node_Id = "NodeID:" + node_id;
                                    self.rank_Id = "RankID:" + index + ' (in ' + self.select_minotype.toLowerCase() + 's)'
                                }
                            }
                        })
                        .on("mouseout", function () {
                            self.node_Id = false
                        });


                    // 添加原始svg缩放事件
                    // 新建d3缩放对象
                    var origin_zoomer = d3.zoom()
                        .scaleExtent([1, 100])
                        .on("zoom", () => {
                            //先暂时关闭鱼眼特效,即恢复原始大小效果
                            var scale_size = d3.event.transform.k;
                            if (scale_size !== 1) {
                                parentNode.setAttribute("open-fisheye", false);
                            }
                            g.attr('transform', d3.event.transform);

                            //如果是缩放为原来大小，则再打开鱼眼特效
                            if (scale_size === 1 && parentNode.getAttribute("open-fisheye") === 'false') {
                                parentNode.setAttribute("open-fisheye", true);
                            }
                        });
                    //绑定缩放事件
                    origin_svg.call(origin_zoomer);

                    //图节点和边信息
                    //删除之前的
                    d3.select(".origin-graph .edge_tag").remove();
                    d3.select(".origin-graph .node_tag").remove();
                    var info_edge_tag = document.createElement('div');
                    info_edge_tag.className = 'edge_tag';
                    info_edge_tag.style.float = 'right';
                    info_edge_tag.style.marginRight = '5px';
                    info_edge_tag.style.marginTop = '0.5%';
                    info_edge_tag.style.boxSizing = 'border-box';
                    info_edge_tag.style.padding = '0 5px';
                    info_edge_tag.style.color = '#666666';
                    info_edge_tag.innerHTML = '<b>Edges</b> ' + self.graph_record['edge_num'];
                    parentNode.appendChild(info_edge_tag);
                    var info_node_tag = document.createElement('div');
                    info_node_tag.className = 'node_tag';
                    info_node_tag.style.float = 'right';
                    info_node_tag.style.marginRight = '5px';
                    info_node_tag.style.marginTop = '0.5%';
                    info_node_tag.style.boxSizing = 'border-box';
                    info_node_tag.style.padding = '0 5px';
                    info_node_tag.style.color = '#666666';
                    info_node_tag.innerHTML = '<b>Nodes</b> ' + self.graph_record['node_num'];
                    parentNode.appendChild(info_node_tag);


                    //稀有结构选择
                    //删除之前的
                    d3.select(".origin-graph .minotype_text").remove();
                    d3.select(".origin-graph .minotype_button").remove();
                    var minotype_text = document.createElement('span');
                    minotype_text.className = 'minotype_text';
                    minotype_text.style.color = '#999999';
                    minotype_text.style.position = 'absolute';
                    minotype_text.style.bottom = '3.8%';
                    minotype_text.style.left = '11%';
                    minotype_text.style.fontSize = '18px';
                    minotype_text.innerText = 'Minority Structure:';
                    parentNode.appendChild(minotype_text);
                    var minotype_button = document.createElement('div');
                    minotype_button.className = 'minotype_button';
                    minotype_button.style.overflow = 'hidden';
                    minotype_button.style.position = 'absolute';
                    minotype_button.style.bottom = '4%';
                    minotype_button.style.left = '33%';
                    minotype_button.style.width = '60%';
                    minotype_button.style.right = '10%';
                    var minotypeList = ['Super Pivot', 'Huge Star', 'Rim', 'Tie'];
                    var minotypeColor = {
                        'Super Pivot': 'rgb(166, 126, 177)',
                        'Huge Star': 'rgb(227, 136, 74)',
                        'Rim': 'rgb(123, 187, 109)',
                        'Tie': 'rgb(63, 141, 183)'
                    };
                    minotypeList.forEach(function (obj, index) {
                        var input = document.createElement('input');
                        input.className = obj.replace("Super ", "").replace("Huge ", "");
                        input.type = 'checkbox';
                        input.style.margin = '0px 5px 0 11px';
                        input.name = 'category';
                        input.value = obj;
                        input.onclick = function () {
                            if (obj === self.select_minotype) {
                                self.select_minotype = null;
                                origin_svg.selectAll('circle').style("fill", "rgb(204, 204, 204)");
                            } else {
                                self.select_minotype = obj;
                                for (var i = 0; i < 4; i++) {
                                    if (i !== index) {
                                        document.querySelectorAll(".origin-graph input[type='checkbox']")[i].checked = false
                                    }
                                }
                                //将所有的节点还原为灰色
                                origin_svg.selectAll('circle').style("fill", "rgb(204, 204, 204)");
                                if (self.graph_record['minority'][self.select_minotype].length === 0) {
                                    document.getElementById("no_tip").style.visibility = 'visible';
                                    setTimeout(function () {
                                        document.getElementById("no_tip").style.visibility = 'hidden';
                                    }, 1000)
                                } else {
                                    self.graph_record['minority'][self.select_minotype].forEach(function (item, index) {
                                        var current_node = origin_svg.select("circle.id_" + item);
                                        current_node.style("fill", minotypeColor[obj])
                                    })
                                }
                            }
                        };
                        var input_span = document.createElement('span');
                        input_span.style.color = "#999999";
                        input_span.innerText = obj;
                        minotype_button.appendChild(input);
                        minotype_button.appendChild(input_span)
                    });
                    parentNode.appendChild(minotype_button);

                    //鱼眼效果
                    g.on("mousemove", function () {
                        if (parentNode.getAttribute("open-fisheye") === 'true') {
                            var fisheye = d3_fisheye.circular()
                                // .radius(120);
                                .radius(svgWidth / 5);
                            fisheye.focus(d3.mouse(this));
                            node.each(function (d) {
                                d.fisheye = fisheye(d);
                            })
                                .attr("cx", function (d) {
                                    return d.fisheye.x;
                                })
                                .attr("cy", function (d) {
                                    return d.fisheye.y;
                                })
                                .attr("r", function (d) {
                                    return d.fisheye.z * d.r * 1.6;
                                });

                            link.attr("x1", function (d) {
                                return data.nodes[d.source].fisheye.x
                            })
                                .attr("y1", function (d) {
                                    return data.nodes[d.source].fisheye.y
                                })
                                .attr("x2", function (d) {
                                    return data.nodes[d.target].fisheye.x
                                })
                                .attr("y2", function (d) {
                                    return data.nodes[d.target].fisheye.y
                                });
                        }
                    });

                    self.LoadingOrigin = false;
                });
            },
            //运行算法
            run_sampling() {
                var self = this;
                //检测设置值是否合法
                if (!this.Graph_Data_Set_value) {
                    this.$message('Please select Graph Data Set');
                    return;
                }
                if (!this.algorithm_value) {
                    this.$message('Please select sampling algorithm');
                    return;
                }
                for (var index in this.param_settings) {
                    if (!this.param_settings[index]) {
                        this.$message('Please set parameter - ' + index);
                        return;
                    } else {
                        var param_value = this.param_settings[index];
                        if (index === 'rate' && (!/^[0-9]*\.{0,1}[0-9]*$/.test(param_value) || !(parseFloat(param_value) > 0 && parseFloat(param_value) < 1))) {
                            this.$message.error('Please set valid parameter - ' + index);
                            return;
                        } else if (index === 'alpha' && (!/^[0-9]*\.{0,1}[0-9]*$/.test(param_value) || parseFloat(param_value) < 1)) {
                            this.$message.error('Please set valid parameter - ' + index);
                            return;
                        } else if (index === 'beta' && (!/^[0-9]*\.{0,1}[0-9]*$/.test(param_value) || !(parseFloat(param_value) >= 1))) {
                            this.$message.error('Please set valid parameter - ' + index);
                            return;
                        } else if (index === 'loss weight' && (!/^[0-9]*\.{0,1}[0-9]*.{1}[0-9]*\.{0,1}[0-9]*.{1}[0-9]*\.{0,1}[0-9]*$/.test(param_value))) {
                            this.$message.error('Please set valid parameter - ' + index);
                            return;
                        }
                    }
                }

                this.LoadingSampling = true;
                // 渲染采样结果并更新展示队列
                setTimeout(() => {
                    self.initSamplingGraph();
                }, 350)

            },
            // 加载原始图
            initOriginGraph() {
                var self = this;
                //更新当前图标识和顶部设置栏
                self.current_graph = self.Graph_Data_Set_value;
                self.algorithm_value = '';
                self.param_settings = {
                    'rate': '',
                    'alpha': '1',
                    'beta': '2',
                    'loss weight': '1,0,0',
                };
                self.select_minotype = '';

                //删除采样图列表
                self.left_num = 1;
                self.right_num = 2;
                self.queue_num = 0;
                d3.selectAll('.sampling-item').remove();
                axios.post("/get_graph_info/", {
                    graph_name: self.Graph_Data_Set_value
                }).then(response => {
                    self.anomaly_total = response.data.anomaly_total;
                    self.graph_record = response.data.graph_record;
                });
                self.LoadingOrigin = true;
                //有鱼眼效果的绘图
                setTimeout(()=>{
                    self.loading_Original_Graph()
                },1000);
            },
            // 加载采样图并更新展示队列
            initSamplingGraph() {
                var self = this;
                var queue_List = document.querySelectorAll(".sampling-item");

                //先将列表svg恢复原位
                for (var i = 0; i < queue_List.length; i++) {
                    var new_left = 1 + 50 * (queue_List.length - i);
                    if (new_left > 151) {
                        d3.select(queue_List[i]).remove();
                        self.queue_num -= 1;
                    } else {
                        queue_List[i].style.left = new_left + "%";
                    }
                }

                this.left_num = 1;
                this.right_num = 2;

                //创建新的采样图框元素
                var sampling_div = document.createElement('div');
                sampling_div.className = 'sampling-item';
                sampling_div.style.left = '1%';
                sampling_div.style.float = 'left';
                sampling_div.style.position = 'absolute';
                sampling_div.style.top = '4%';
                sampling_div.style.width = '48%';
                sampling_div.style.height = '95%';

                axios.post("/run_sampling/", {
                    graph_name: self.Graph_Data_Set_value,
                    algorithm_name: self.algorithm_value,
                    param_settings: self.param_settings
                }).then(response => {
                    if (response.data.state === "success") {
                        let sampling_graph_record = response.data.graph_record;
                        sampling_div.setAttribute("open-fisheye", true);
                        d3.json(self.samping_fisheye_file).then(function (data) {

                            var current_algorithm = self.algorithm_value;
                            var select_minotype = null;
                            //采样算法名称标识
                            var span_algorithm = document.createElement('span');
                            span_algorithm.style.position = 'absolute';
                            span_algorithm.style.top = '1%';
                            span_algorithm.style.left = '1%';
                            span_algorithm.style.color = '#666666';
                            span_algorithm.style.zIndex = '1000';
                            span_algorithm.innerHTML = '<b>Algorithm</b> {algorithm}'.format({algorithm: self.algorithm_value});
                            sampling_div.appendChild(span_algorithm);

                            //采样算法参数标识
                            var span_parameters = document.createElement('span');
                            span_parameters.style.position = 'absolute';
                            span_parameters.style.top = '4%';
                            span_parameters.style.left = '1%';
                            span_parameters.style.color = '#666666';
                            span_parameters.style.zIndex = '1000';

                            var parameters = 'sampling rate={rate}'.format({rate: self.param_settings.rate});

                            self.algorithm_list[self.algorithm_value].options.forEach(function (obj, index) {
                                parameters = parameters + '&nbsp;&nbsp;&nbsp;' + obj + '=' + self.param_settings[obj];
                            });
                            parameters = '<b>Paramters</b> ' + parameters;
                            span_parameters.innerHTML = parameters;
                            sampling_div.appendChild(span_parameters);

                            //关闭采样框标识
                            var close_tag = document.createElement('i');
                            close_tag.style.position = 'absolute';
                            close_tag.style.right = '0.5%';
                            close_tag.style.top = '0.8%';
                            close_tag.style.cursor = 'pointer';
                            close_tag.className = 'sampling-close-btn el-icon-close';
                            close_tag.style.visibility = 'hidden';
                            close_tag.onclick = function (e) {
                                var target_element = e.currentTarget;
                                var parentNode = target_element.parentNode;
                                var sampling_list = document.querySelectorAll(".sampling-item");
                                var sampling_length = sampling_list.length;
                                var old_left = null;
                                for (let i = 0; i < sampling_length; i++) {
                                    if (sampling_list[i] === parentNode) {
                                        d3.select(parentNode).remove();
                                        self.queue_num -= 1;
                                        if (self.left_num < sampling_length - i) {
                                            //删的是右边
                                            old_left = sampling_length - i - 2;
                                        } else {
                                            //删的是左边
                                            old_left = self.left_num - 1;
                                            if (old_left <= 0) {
                                                old_left = 1
                                            }
                                        }
                                        break
                                    }
                                }
                                //重新排列
                                var new_sampling_list = document.querySelectorAll(".sampling-item");
                                for (var i = 0; i < new_sampling_list.length; i++) {
                                    new_sampling_list[i].style.left = 1 + 50 * (new_sampling_list.length - i - 1) + "%";
                                }
                                self.left_num = 1;
                                self.right_num = 2;

                            };
                            sampling_div.appendChild(close_tag);

                            //要先从data里获取svg头信息，设置svg元素属性
                            var sampling_svg = d3.select(sampling_div).append("svg")
                                .attr("width", data.svg_width)
                                .attr("height", data.svg_height)
                                .attr("viewBox", data.svg_viewBox);

                            var link = sampling_svg.selectAll(".link")
                                .data(data.links)
                                .enter().append("line")
                                .attr("class", "link")
                                .attr("x1", function (d) {
                                    return data.nodes[d.source].cx
                                })
                                .attr("y1", function (d) {
                                    return data.nodes[d.source].cy
                                })
                                .attr("x2", function (d) {
                                    return data.nodes[d.target].cx
                                })
                                .attr("y2", function (d) {
                                    return data.nodes[d.target].cy
                                }).style("stroke", "#bcbcbc");

                            var node = sampling_svg.selectAll(".node")
                                .data(data.nodes)
                                .enter().append("circle")
                                .attr("class", function (d) {
                                    return d.id;
                                })
                                .attr("cx", function (d) {
                                    return d.cx;
                                })
                                .attr("cy", function (d) {
                                    return d.cy;
                                })
                                .attr("r", function (d) {
                                    return d.r * 1.5
                                })
                                .style("fill", "#cccccc")
                                .style("stroke", "#666666");

                            var container = document.getElementById('sampling-graph');
                            container.insertBefore(sampling_div, null);//插入到最左边

                            var parent_Width = sampling_div.clientWidth;
                            var parent_Height = sampling_div.clientHeight;
                            sampling_svg = d3.selectAll(".sampling-item svg:nth-last-child(1)");
                            let svgWidth = sampling_svg.attr("width").replace("px", "");
                            let svgHeight = sampling_svg.attr("height").replace("px", "");
                            var margin = {left: 20, right: 20, top: 20, bottom: 20};
                            var scaleNumber = d3.min([(parent_Width - margin.left - margin.right) / svgWidth, (parent_Height - margin.top - margin.bottom) / svgHeight]);
                            var sampling_svgWidth = svgWidth * scaleNumber;
                            var sampling_svgHeight = svgHeight * scaleNumber;
                            sampling_svg
                                .attr("width", sampling_svgWidth)
                                .attr("height", sampling_svgHeight)
                                .style("position", "absolute")
                                .style("left", (parent_Width - sampling_svgWidth) / 2 + "px")
                                .style("top", (parent_Height - sampling_svgHeight) / 2 + "px");

                            //为节点和边设置样式(用于提示用户当前为新的框)
                            sampling_svg.selectAll("circle")
                                .style("stroke", "#666666")
                                .style("fill", "#cccccc");
                            sampling_svg.selectAll("path")
                                .style("stroke", "#333333");

                            //添加鼠标hover标注事件
                            sampling_svg.selectAll("circle")
                                .on("mouseover", function () {
                                    if (select_minotype) {
                                        var current_node = d3.select(this);
                                        var node_id = parseInt(current_node.attr("class").replace("id_", ""));
                                        var index = (sampling_graph_record['minority'][select_minotype]).findIndex(item => item === node_id) + 1;
                                        self.label_tip.style.left = d3.event.pageX + 15 + "px";
                                        self.label_tip.style.top = d3.event.pageY + 5 + "px";
                                        if (index > 0) {
                                            self.node_Id = "NodeID:" + node_id;
                                            self.rank_Id = "RankID:" + index + ' (in ' + select_minotype.toLowerCase() + 's)'
                                        }
                                    }
                                })
                                .on("mouseout", function () {
                                    self.node_Id = false
                                });

                            // 添加采样svg缩放事件
                            var sampling_zoomer = d3.zoom()
                                .scaleExtent([1, 100])
                                .on("zoom", () => {
                                    //先暂时关闭鱼眼特效,即恢复原始大小效果
                                    var scale_size = d3.event.transform.k;
                                    if (scale_size !== 1) {
                                        sampling_div.setAttribute("open-fisheye", false);
                                    }

                                    sampling_svg.selectAll("line")
                                        .attr("x1", function (d) {
                                            return data.nodes[d.source].cx
                                        })
                                        .attr("y1", function (d) {
                                            return data.nodes[d.source].cy
                                        })
                                        .attr("x2", function (d) {
                                            return data.nodes[d.target].cx
                                        })
                                        .attr("y2", function (d) {
                                            return data.nodes[d.target].cy
                                        });
                                    sampling_svg.selectAll("circle")
                                        .attr("cx", function (d) {
                                            return d.cx;
                                        })
                                        .attr("cy", function (d) {
                                            return d.cy;
                                        })
                                        .attr("r", function (d) {
                                            return d.r * 1.5
                                        });
                                    //将线和节点全部缩放
                                    sampling_svg.selectAll('line').attr("transform", d3.event.transform);
                                    sampling_svg.selectAll('circle').attr("transform", d3.event.transform);


                                    //如果是缩放为原来大小，则再打开鱼眼特效
                                    if (scale_size === 1 && sampling_div.getAttribute("open-fisheye") === 'false') {
                                        sampling_div.setAttribute("open-fisheye", true);
                                    }
                                });
                            // 绑定缩放事件
                            sampling_svg.call(sampling_zoomer);

                            //图节点和边信息
                            var info_edge_tag = document.createElement('div');
                            info_edge_tag.style.float = 'right';
                            info_edge_tag.style.marginRight = '3.5%';
                            info_edge_tag.style.marginTop = '1%';
                            info_edge_tag.style.boxSizing = 'border-box';
                            info_edge_tag.style.padding = '0 5px';
                            info_edge_tag.style.color = '#666666';
                            info_edge_tag.innerHTML = '<b>Edges</b> ' + sampling_graph_record['edge_num'];
                            sampling_div.appendChild(info_edge_tag);
                            var info_node_tag = document.createElement('div');
                            info_node_tag.style.float = 'right';
                            info_node_tag.style.marginRight = '1%';
                            info_node_tag.style.marginTop = '1%';
                            info_node_tag.style.boxSizing = 'border-box';
                            info_node_tag.style.padding = '0 5px';
                            info_node_tag.style.color = '#666666';
                            info_node_tag.innerHTML = '<b>Nodes</b> ' + sampling_graph_record['node_num'];
                            sampling_div.appendChild(info_node_tag);


                            // 无稀有结构时的提示框
                            var minotype_tip = document.createElement('div');
                            minotype_tip.style.visibility = 'hidden';
                            minotype_tip.setAttribute("role", "tooltip");
                            minotype_tip.setAttribute("aria-hidden", "false");
                            minotype_tip.setAttribute("class", "el-popover el-popper el-popover--plain my-no-tip");
                            minotype_tip.setAttribute("tabindex", "0");
                            minotype_tip.setAttribute("x-placement", "bottom");
                            minotype_tip.style.position = 'absolute';
                            minotype_tip.style.left = '50%';
                            minotype_tip.style.top = '45%';
                            minotype_tip.style.transform = 'translate(-50%, -50%)';
                            sampling_div.appendChild(minotype_tip);

                            //添加稀有结构选择
                            var minotype_text = document.createElement('span');
                            minotype_text.style.color = '#999999';
                            minotype_text.style.position = 'absolute';
                            minotype_text.style.bottom = '2.8%';
                            minotype_text.style.left = '11%';
                            minotype_text.style.fontSize = '18px';
                            minotype_text.innerText = 'Minority Structure:';
                            sampling_div.appendChild(minotype_text);
                            var minotype_button = document.createElement('div');
                            minotype_button.style.overflow = 'hidden';
                            minotype_button.style.position = 'absolute';
                            minotype_button.style.bottom = '3%';
                            minotype_button.style.left = '33%';
                            minotype_button.style.width = '60%';
                            minotype_button.style.right = '10%';
                            var minotypeList = ['Super Pivot', 'Huge Star', 'Rim', 'Tie'];
                            var minotypeColor = {
                                'Super Pivot': 'rgb(166, 126, 177)',
                                'Huge Star': 'rgb(227, 136, 74)',
                                'Rim': 'rgb(123, 187, 109)',
                                'Tie': 'rgb(63, 141, 183)'
                            };
                            minotypeList.forEach(function (obj, index) {
                                var input = document.createElement('input');
                                input.className = obj.replace("Super ", "").replace("Huge ", "");
                                input.type = 'checkbox';
                                input.style.margin = '0px 5px 0 11px';
                                input.name = 'category';
                                input.value = obj;
                                input.onclick = function () {
                                    if (obj === select_minotype) {
                                        select_minotype = null;
                                        sampling_svg.selectAll('circle').style("fill", "rgb(204, 204, 204)");
                                    } else {
                                        select_minotype = obj;
                                        for (var i = 0; i < 4; i++) {
                                            if (i !== index) {
                                                sampling_div.querySelectorAll("input[type='checkbox']")[i].checked = false
                                            }
                                        }
                                        //将所有的节点还原为灰色
                                        sampling_svg.selectAll('circle').style("fill", "rgb(204, 204, 204)");
                                        if (sampling_graph_record['minority'][select_minotype].length === 0) {
                                            minotype_tip.innerHTML = `There are no <b>${select_minotype}s</b> in this graph`;
                                            minotype_tip.style.visibility = 'visible';
                                            setTimeout(function () {
                                                minotype_tip.style.visibility = 'hidden';
                                            }, 1000)
                                        } else {
                                            sampling_graph_record['minority'][select_minotype].forEach(function (item, index) {
                                                var current_node = sampling_svg.select("circle.id_" + item);
                                                current_node.style("fill", minotypeColor[obj])
                                            })
                                        }
                                    }
                                };
                                var input_span = document.createElement('span');
                                input_span.style.color = "#999999";
                                input_span.innerText = obj;
                                minotype_button.appendChild(input);
                                minotype_button.appendChild(input_span)
                            });
                            sampling_div.appendChild(minotype_button);


                            sampling_div.onmouseenter = function () {
                                close_tag.style.visibility = 'visible';
                            };
                            sampling_div.onmouseleave = function () {
                                close_tag.style.visibility = 'hidden';
                            };

                            self.queue_num += 1;
                            self.right_num = 2;
                            self.LoadingSampling = false;

                            //鱼眼效果
                            sampling_svg.on("mousemove", function () {
                                if (sampling_div.getAttribute("open-fisheye") === "true") {
                                    var fisheye = d3_fisheye.circular()
                                        .radius(svgWidth / 5);
                                    fisheye.focus(d3.mouse(this));
                                    node.each(function (d) {
                                        d.fisheye = fisheye(d);
                                    })
                                        .attr("cx", function (d) {
                                            return d.fisheye.x;
                                        })
                                        .attr("cy", function (d) {
                                            return d.fisheye.y;
                                        })
                                        .attr("r", function (d) {
                                            return d.fisheye.z * d.r * 1.6;
                                        });

                                    link.attr("x1", function (d) {
                                        return data.nodes[d.source].fisheye.x
                                    })
                                        .attr("y1", function (d) {
                                            return data.nodes[d.source].fisheye.y
                                        })
                                        .attr("x2", function (d) {
                                            return data.nodes[d.target].fisheye.x
                                        })
                                        .attr("y2", function (d) {
                                            return data.nodes[d.target].fisheye.y
                                        });
                                }
                            });
                        });
                    } else {
                        console.log("Error");
                    }
                });
            },
            //左右浏览切换
            switch_view(director) {
                var queue_List = document.querySelectorAll(".sampling-item");
                var unit = this.queue_num === 4 ? 2 : 1;
                if (director === 'left') {
                    //左移
                    this.left_num -= unit;
                    this.right_num -= unit;
                    queue_List.forEach(function (obj, index) {
                        var new_left = parseInt(obj.style.left.replace("%", "")) + 50 * unit;
                        obj.style.left = new_left + "%";
                    });
                } else if (director === 'right' && this.queue_num > 2) {
                    //右移
                    this.left_num += unit;
                    this.right_num += unit;
                    queue_List.forEach(function (obj, index) {
                        var new_left = parseInt(obj.style.left.replace("%", "")) - 50 * unit;
                        obj.style.left = new_left + "%";
                    });
                }

            },
        },
        computed: {
            get_option_labels: function () {
                if (this.algorithm_value !== '') {
                    return this.algorithm_list[this.algorithm_value]['options']
                } else {
                    return []
                }
            },
            origin_fisheye_file: function () {
                return `/static/dataset/json_files/origin_${this.Graph_Data_Set_value.toLowerCase()}.json`
            },
            samping_fisheye_file: function () {
                return '/static/sampling_result.json';
            }
        }
    }
</script>

